<template>
  <nv-card>
    <div class="flex flex-wrap-reverse items-center mb-base">
      <vs-button class="p-3 mr-4" @click="cancel" color="primary" type="filled" icon="fa-chevron-left" icon-pack="far">{{ $t('BACK') }}</vs-button>
    </div>
    <div class="row hover">
      <p class="info-row">
        <span>{{ $t('ID') }}:</span>
        <span>{{ roleType.roleTypeId }}</span>
      </p>
      <p class="info-row">
        <span>{{ $t('ROLE_NAME') }}:</span>
        <span>{{ roleType.description | placeholder('-') }}</span>
      </p>
      <p class="info-row" v-if="roleType.parentTypeId">
        <span>{{ $t('PARENT_ROLE') }}: </span>
        <span><router-link :to="{ name: 'ViewRoleType', params: { id: roleType.parentTypeId } }" :title="roleType.parentTypeDescription">{{ roleType.parentTypeDescription }}</router-link></span>
      </p>
    </div>
  </nv-card>
</template>

<script>
import RoleTypeService from '@/services/commons/RoleTypeService'
import DisabledClassMixin from '@/mixins/commons/DisabledClassMixin'

export default {
  name: 'role-type-view',
  mixins: [DisabledClassMixin],
  data: function () {
    return {
      roleType: {},
      popupActive: false
    }
  },
  methods: {
    async load () {
      let self = this
      RoleTypeService.roleTypes({
        roleTypeId: this.$route.params.id
      }).then(function (response) {
        self.roleType = response.data.typeList[0] || {}
      })
    },
    cancel () {
      this.$router.back()
    },
    modify () {
      this.$router.push({ name: 'ModifyRoleType', params: { id: this.$route.params.id } })
    }
  },
  mounted () {
    this.load()
  },
  watch: {
    $route (to, from) {
      this.load()
    }
  }
}
</script>
